
// Imports
import { ref } from "vue";
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonRow,
  IonGrid,
  IonCol,
  IonItem,
  IonInput,
  IonButton,
  IonImg,
} from "@ionic/vue";
import { defineComponent } from "vue";
import { useRouter } from 'vue-router';
import { useStore } from 'vuex'

export default defineComponent({
  name: "Login",
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonRow,
    IonGrid,
    IonCol,
    IonItem,
    IonInput,
    IonButton,
    IonImg,
  },
  setup() {
    // Define router
    const router = useRouter();

    // Define store
    const store = useStore();

    // Define form values
    const email = ref("");
    const password = ref("");

    // Define submit method
    const submit = async () => {
      await store.dispatch("login", {email: email.value, password: password.value});
      router.push({name: 'Home'});
    };

    return { email, password, submit };
  },
});
