
// Imports
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonRow,
  IonGrid,
  IonCol,
  IonButton,
} from "@ionic/vue";
import { defineComponent } from "vue";
import store from '@/store';
import { useRouter } from "vue-router";

export default defineComponent({
  name: "Settings",
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonRow,
    IonGrid,
    IonCol,
    IonButton,
  },
  setup() {
    // Define router
    const router = useRouter();

    // logout function
    const logout = async () => {
      await store.dispatch('logout');
      router.replace({ name: "Login" });
    };

    return { logout };
  },
});
