
// Imports
import { add, refresh, list, settings, person } from "ionicons/icons";
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonRow,
  IonGrid,
  IonCol,
  IonIcon,
  IonButton,
} from "@ionic/vue";
import { defineComponent, reactive } from "vue";
import { useRouter } from "vue-router";

export default defineComponent({
  name: "Home",
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonRow,
    IonGrid,
    IonCol,
    IonIcon,
    IonButton,
  },
  setup() {
    // Define router
    const router = useRouter();

    // Array of buttons
    const buttons = reactive([
      {
        name: "newObject",
        icon: add,
        route: "NewObjectData",
      },
      {
        name: "existingObject",
        icon: refresh,
      },
      {
        name: "objectDirectory",
        icon: list,
      },
      {
        name: "settings",
        icon: settings,
        route: "Settings"
      },
      {
        name: "profile",
        icon: person,
      },
    ]);

    // push function
    const push = (route: string) => {
      router.push({ name: route });
    };

    return { buttons, push };
  },
});
